@import url("https://fonts.googleapis.com/css?family=Roboto");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons");
* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: inherit; }

.fade-enter {
  opacity: 0; }

.fade-enter-active {
  opacity: 1;
  transition: opacity 600ms ease-out; }

.fade-exit,
.fade-exit-active {
  opacity: 0; }

.animation-fade-in {
  animation: fade-in 600ms ease-out; }

@keyframes fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.animation-correct-answer {
  animation: correct-answer 2s ease-in-out both; }

@keyframes correct-answer {
  0% {
    background-color: #6e6969; }
  30% {
    background-color: #59be05; }
  60% {
    background-color: #6e6969; }
  100% {
    background-color: #59be05; } }

.admin-panel {
  display: flex;
  flex-wrap: nowrap; }

.app {
  height: 100vh;
  overflow: hidden;
  color: #5F5B6B; }

.homepage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 16px; }
  .homepage__title {
    font-size: 100px;
    font-weight: bold; }

.test {
  margin: 16px; }
  .test__hidden {
    display: none; }

.training {
  margin: 16px; }
  .training__hidden {
    display: none; }

.button {
  padding: 8px 16px;
  cursor: pointer;
  background: #014d11;
  color: white;
  font-size: 24px;
  border: 0;
  border-radius: 2px; }
  .button__float {
    width: 40px;
    height: 40px;
    position: fixed;
    left: 20px;
    top: 15px;
    border: none;
    border-radius: 50%;
    background-color: white;
    color: #014d11; }
  .button__iconButton {
    border: 0;
    background: none;
    color: #014d11; }
  .button__icon {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    /* Preferred icon size */
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;
    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;
    /* Support for IE. */
    font-feature-settings: 'liga'; }

.navigation {
  color: white;
  background: #014d11; }
  .navigation__list {
    display: flex;
    justify-content: flex-end;
    margin: 0;
    list-style-type: none; }
    .navigation__list__item {
      margin: 20px;
      padding: 8px 16px;
      border: 0;
      border-radius: 2px;
      background: #6e6969; }

.score-counter {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 16px;
  font-size: 30px;
  padding: 16px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2); }
  .score-counter__hidden {
    display: none; }

.rules {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 16px;
  padding: 16px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  opacity: 1;
  will-change: opacity;
  transition: opacity 500ms ease-out; }
  .rules__text {
    font-size: 25px; }
  .rules__start {
    margin-top: 16px;
    max-width: 200px; }
  .rules__hidden {
    opacity: 0;
    height: 0; }

.timer {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
  padding: 20px 10px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  opacity: 1;
  will-change: opacity;
  transition: opacity 500ms ease-out; }
  .timer__bar {
    width: 100%;
    height: 20px;
    border-radius: 20px;
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.3); }
    .timer__bar__progress {
      border-radius: 20px;
      height: 20px;
      background: linear-gradient(to top, rgba(255, 255, 255, 0.25), rgba(0, 0, 0, 0.25)), linear-gradient(to right, #979796, #00cc4e); }
  .timer__counter {
    top: 0;
    position: absolute;
    margin: 0 50%; }
  .timer__hidden {
    opacity: 0; }

.question-box {
  margin-bottom: 16px;
  padding: 16px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  opacity: 1; }
  .question-box__hidden {
    opacity: 0;
    will-change: opacity;
    transition: opacity 200ms ease-in; }
  .question-box__visible {
    will-change: opacity;
    transition: opacity 200ms ease-out;
    opacity: 1; }
  .question-box__text {
    display: block;
    min-height: 80px;
    font-size: 30px;
    margin-bottom: 16px; }
  .question-box__expired {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .question-box__answers {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    min-height: 300px;
    margin-top: 16px;
    color: white; }
    .question-box__answers__answer {
      padding: 16px 16px;
      width: 40%;
      background-color: #6e6969;
      cursor: pointer; }
      .question-box__answers__answer:hover {
        background-color: #2E0E02; }
    .question-box__answers__correct {
      background-color: green !important; }
    .question-box__answers__wrong {
      background-color: #FF5E5B !important; }

.question-data-row__answer {
  display: block; }

.question-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 432px;
  height: calc(100vh - 75px);
  padding: 16px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2); }
  .question-form__textarea {
    width: 100%;
    resize: none;
    padding: 10px;
    margin-bottom: 16px;
    font-size: 16px; }
  .question-form__button {
    margin-top: 16px; }
  .question-form__hidden {
    display: none; }

.universal-table {
  max-height: calc(100vh - 107px);
  width: 100%;
  margin: 16px 0 0 16px;
  overflow-y: scroll;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  font-size: 13px;
  line-height: 2rem; }
  .universal-table__table {
    width: 100%;
    border-collapse: collapse; }
  .universal-table__header {
    font-size: 18px;
    padding: 16px 16px 16px 0;
    font-weight: inherit; }
  .universal-table td {
    padding: 10px;
    border-top: 1px solid rgba(0, 0, 0, 0.12); }



